@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap);
*{margin:0;padding:0;box-sizing:border-box}html{font-family:'Open Sans', sans-serif;font-size:100%}@media (max-width: 740px){html{font-size:10px}}.App{width:100%;height:100vh}.container{max-width:1200px;margin:0 auto;padding:0 15px}

.Popup{position:absolute;top:-55%;left:50%;-webkit-transform:translate(-50%);transform:translate(-50%);padding:5px;max-width:300px;background-color:#4895ef;color:#ffffff;font-size:0.625rem;display:none;white-space:nowrap}.Popup--active{display:block}

.conventer__curr-bar-item{position:relative;width:70%;padding:12px 0;border-top:1px solid #cdcdcd;border-bottom:1px solid #cdcdcd;border-right:1px solid #cdcdcd;display:flex;align-items:center;justify-content:center;cursor:pointer}.conventer__curr-bar-item:first-child{border-radius:4px 0 0 4px;border-left:1px solid #cdcdcd}.conventer__curr-bar-item:last-child{border-radius:0 4px 4px 0}.conventer__curr-bar-item--active{background-color:#4895ef;color:#ffffff}@media (max-width: 460px){.conventer__curr-bar-item{width:30px}}

.conventer__curr-bar{flex-basis:30%;display:flex;justify-content:flex-start}

.CurrencyListItem{padding:5px;display:flex;justify-content:space-between;align-items:center;grid-gap:5px;gap:5px;font-size:0.625rem;cursor:pointer}.CurrencyListItem:hover{background-color:#4895ef;color:#ffffff}.CurrencyListItem__CharCode{font-weight:700}

.CurrencyList{position:absolute;padding:10px;top:47%;left:0;width:100%;background:#ffff;color:black;border:1px solid #cdcdcd;border-radius:4px;display:none}@media (max-width: 460px){.CurrencyList{top:20%}}.CurrencyList--active{display:grid;grid-template-columns:repeat(3, 1fr);grid-column-gap:50px;-webkit-column-gap:50px;column-gap:50px;grid-row-gap:10px;row-gap:10px}@media (max-width: 460px){.CurrencyList--active{grid-column-gap:25px;-webkit-column-gap:25px;column-gap:25px}}

.conventer__calc-input{width:100%;height:50px;outline:none;border:none;font-size:2rem}

.conventer__wrapper{position:relative;width:90%;margin:0 auto}.conventer__header{margin-top:0.625rem;width:100%;display:flex;justify-content:space-between;align-items:center}.conventer__header-item{flex-basis:30%;display:flex;flex-flow:column;grid-gap:0.625rem;gap:0.625rem}.conventer__title{font-size:1rem;color:#777;font-weight:400}.conventer__calc{margin-top:1.5rem;display:flex;justify-content:space-between;align-items:center}@media (max-width: 460px){.conventer__calc{flex-flow:column;grid-gap:10px;gap:10px}}.conventer__calc-item{width:40%;height:100px;padding:0.625rem;border:1px solid #cdcdcd}@media (max-width: 460px){.conventer__calc-item{width:340px}}.conventer__calc-info{margin-top:0.625rem;font-size:1rem}.conventer__calc-img{width:50px;height:50px}.conventer__data-info{margin-top:2rem;font-size:1rem;color:gray}

.NavBar{min-height:100px;display:flex;justify-content:flex-start;align-items:center;grid-gap:5%;gap:5%;border-top:1px solid gray;border-bottom:1px solid gray}.nav-title{text-align:center;font-size:1.5rem;font-weight:700}.nav-items{display:flex;justify-content:flex-start;align-items:center;grid-gap:1rem;gap:1rem}.nav-item{white-space:nowrap;font-size:1rem;font-weight:400;text-decoration:none;color:black}.nav-item--active{color:#4895ef;text-decoration:underline}

.courses-header{margin-top:0.625rem;display:flex;justify-content:flex-start;align-items:center;grid-gap:1rem;gap:1rem}.currencies{display:flex;justify-content:flex-start;align-items:center;grid-gap:0.625rem;gap:0.625rem}.currency-table{margin-top:1rem;width:100%;text-align:left;font-size:calc(10px + 6 * ((100vw - 320px) / 1043));border-spacing:0;border-collapse:separate}td,th{padding:12px 8px;border-bottom:1px solid #e8e9eb}tr{cursor:pointer}.with-hover:hover{background-color:#e8e9eb}

